import React from 'react';
import './Card.css';

const base = 'https://raw.githubusercontent.com/irinainina/rslang/rslang-data/data/';

const renderItems = (num, arr) => {

  return arr.filter((item, index) => index >= num * 20 && index < num * 20 + 20)
    .map(item => {
    const { word, image, audio, audioMeaning, audioExample, textMeaning, textExample, transcription, wordTranslate, textMeaningTranslate, textExampleTranslate, id} = item;
    return (
      <div className="card" key={id}>  
        <h2>{id}. {word}</h2>
        <p>{wordTranslate}</p>
        <p>{transcription}</p>        
        <audio controls src={`${base}${audio}`} />
        <img src={`${base}${image}`} alt={word} /> 
        <p>{textMeaning}</p>
        <p>{textMeaningTranslate}</p>
        <audio controls src={`${base}${audioMeaning}`} />
        <p>{textExample}</p>
        <p>{textExampleTranslate}</p>      
        <audio controls src={`${base}${audioExample}`} />
      </div>
    );
  });
}

function Card({num, source}) {
  const items = renderItems(num, source);
  return (
    <>
      {items}
    </>
  );
}

export default Card;
