import React, {Component} from 'react';
import './App.css';
import Card from '../Card/Card';
import Pagination from '../Pagination/Pagination';
import book1 from '../../data/book1.js';
import book2 from '../../data/book2.js';
import book3 from '../../data/book3.js';
import book4 from '../../data/book4.js';
import book5 from '../../data/book5.js';
import book6 from '../../data/book6.js';
const sources = [book1, book2, book3, book4, book5, book6];

class App extends Component {
  state= {
    source: sources[0],
    num: 0
  }
 
 pageOnClick = (index) => {
   const sourceNum = Math.floor(index / 30);
   const pageNum = index % 30;
   this.setState({
     source: sources[sourceNum],
     num: pageNum
   });
   const body = document.querySelector('body');
   body.scrollIntoView({behavior: "smooth"});
 }

  render() {
    const {source, num} = this.state;
    return (
      <div className="App">
        <Card source={source}
              num={num} />
        <Pagination pageOnClick={this.pageOnClick}/>
      </div>
    );
  }
}

export default App;
